body {
  margin: 0;
}

.header {
  justify-content: space-between;
  align-items: center;
  padding: 0 50px;
  display: flex;
}

.header img {
  margin: 10px 0;
}

.nav_list {
  padding: 0;
  list-style: none;
  display: flex;
}

.nav_item {
  text-transform: uppercase;
  height: 15px;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 600;
  display: inline-block;
}

.nav_item:not(:last-child) {
  margin-right: 20px;
}

.nav_item:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  border: 2px solid #00acdc;
  border-radius: 8px;
  display: block;
  position: relative;
  top: 100%;
}

[data-qa="hover"] {
  color: #00acdc;
}
